import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams } from 'ionic-angular';

import { Trip } from '../../models/trip';
import { Plan } from '../../models/plan';

/**
 * Generated class for the TripViewPlanPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-trip-view-plan',
  templateUrl: 'trip-view-plan.html',
})
export class TripViewPlanPage {

  trip: Trip;
  plan: Plan;

  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController) {
    this.plan = navParams.get('plan') as Plan;
    this.trip = navParams.get('trip') as Trip;
  }

  ionViewDidLoad() {
    console.log('TripViewPlanPage: '+ this.plan);
  }

  editPlan() {
    let addModal = this.modalCtrl.create('TripEditPlanPage', {plan: this.plan});
    addModal.onDidDismiss(newPlan => {
      if (newPlan) {
        this.trip.updatePlan(newPlan, this.plan);
      }
    })
    addModal.present();
  }

}



